import { writable } from 'svelte/store';

// Dfinir l'URL de l'API pour viter les problmes de CORS
const API_URL = 'http://localhost:5000';

// Store pour l'tat d'authentification - initialisation explicite  false
export const isAuthenticated = writable(false);
export const currentUser = writable(null);

// Vrifier le token au dmarrage
export function checkAuth() {
    // Dans un premier temps, tenter de lire le token et l'utilisateur stocks
    if (typeof window === 'undefined' || !window.localStorage) {
        console.log("Environnement sans localStorage, authentification dsactive");
        return false;
    }

    const token = localStorage.getItem('auth_token');
    const userStr = localStorage.getItem('user');

    // Si rien n'est stock  pas authentifi
    if (!token || !userStr) {
        logout();
        return false;
    }

    let user;
    try {
        user = JSON.parse(userStr);
    } catch (e) {
        console.log("Erreur de parsing JSON de l'utilisateur :", e);
        logout();
        return false;
    }

    // On suppose l'utilisateur authentifi le temps de valider le token ct backend
    isAuthenticated.set(true);
    currentUser.set({ ...user });

    // Vrification asynchrone de la validit du token auprs du backend
    fetch(`${API_URL}/api/auth/me`, {
        method: 'GET',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`
        }
    })
    .then(response => {
        console.log("Rponse du serveur pour /me:", response.status);
        if (response.status === 401) {
            // Token rellement invalide ou expir  on dconnecte
            console.log("Token invalide ou expir, dconnexion");
            logout();
        } else if (!response.ok) {
            // Autre erreur serveur : on conserve la session et on log uniquement
            console.warn("Erreur lors de la validation du token (", response.status, ")  session conserve");
        }
    })
    .catch(err => {
        // Typiquement dclench si la requte est annule par un rafrachissement ; ne pas dconnecter
        console.warn("Erreur rseau ou requte annule lors de la vrification du token: ", err);
    });

    return true;
}

// Fonction de dconnexion
export function logout() {
    console.log("Dconnexion utilisateur");
    if (typeof window !== 'undefined' && window.localStorage) {
        localStorage.removeItem('auth_token');
        localStorage.removeItem('user');
    }
    isAuthenticated.set(false);
    currentUser.set(null);
}

// Fonction pour rcuprer le token
export function getToken() {
    if (typeof window === 'undefined' || !window.localStorage) {
        return null;
    }
    return localStorage.getItem('auth_token');
}

// Intercepteur pour les requtes API
export async function fetchWithAuth(url, options = {}) {
    const token = getToken();
    
    // Adapter l'URL en fonction du type de requte
    let apiUrl = url;
    
    // Si l'URL ne contient pas dj http:// et ne commence pas par /api, ajuster le format
    if (!url.includes('http://') && !url.startsWith('/api')) {
        apiUrl = `${API_URL}/api${url.startsWith('/') ? url : `/${url}`}`;
    } else if (url.startsWith('/api')) {
        apiUrl = `${API_URL}${url}`;
    }
    
    console.log("URL modifie pour fetch:", apiUrl);
    
    if (token) {
        options.headers = {
            ...options.headers || {},
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`
        };
        console.log("Envoi requte avec token:", `Bearer ${token}`);
    }
    
    try {
        const response = await fetch(apiUrl, {
            ...options
        });
        
        // Si on reoit une erreur 401, on dconnecte l'utilisateur
        if (response.status === 401) {
            console.log("Erreur 401, dconnexion");
            logout();
        }
        
        return response;
    } catch (error) {
        console.error("Erreur de connexion API:", error);
        throw error;
    }
} 